import React from "react";
import {
  ComposableMap,
  Geographies,
  Geography,
  ZoomableGroup,
} from "react-simple-maps";
import { scaleLinear } from "d3-scale";

import { geoUrl } from "../_context";
import getStateFromId from "../functions/getStateFromId";

const findMatch = (data, geoId) => {
  if (!data) return;
  return data.find((s) => parseInt(s.fips) === parseInt(geoId));
};

const tooltipStyle = {
  hover: {
    fill: "#F53",
    outline: "none",
  },
  pressed: {
    fill: "#E42",
    outline: "none",
  },
};

const MapArea = (props) => {
  const { setTooltipContent, low, high, position, handleMoveEnd, stateData } =
    props;

  const colorScale = scaleLinear()
    .domain([low, 0, high])
    .range(["red", "white", "blue"]);

  const [data, nextData] = stateData;

  return (
    <ComposableMap data-tip="" projection="geoAlbersUsa">
      <ZoomableGroup
        zoom={position.zoom}
        center={position.coordinates}
        onMoveEnd={handleMoveEnd}
      >
        <Geographies geography={geoUrl}>
          {({ geographies }) =>
            geographies.map((geo) => {
              const countyName = geo.properties.name;
              const stateName = getStateFromId(geo.id);

              // redo this section to use checkbox and be more efficient by going down state path
              const cur =
                findMatch(data[0].alabama, geo.id) ||
                findMatch(data[0].iowa, geo.id);
              const next =
                findMatch(nextData[0].alabama, geo.id) ||
                findMatch(nextData[0].iowa, geo.id);
              const geoData = cur
                ? next
                  ? next.data - cur.data
                  : cur.data
                : undefined;

              return (
                <Geography
                  key={geo.rsmKey}
                  geography={geo}
                  fill={colorScale(geoData ? geoData : "#0000FF")}
                  onMouseEnter={() => {
                    setTooltipContent("");
                    setTooltipContent(
                      `${countyName}, ${stateName} : ${geoData && geoData}`
                    );
                  }}
                  onMouseLeave={() => {
                    setTooltipContent("");
                  }}
                  style={tooltipStyle}
                />
              );
            })
          }
        </Geographies>
      </ZoomableGroup>
    </ComposableMap>
  );
};

export default MapArea;
